.main {
    padding: 20px;
    height: 100vh;
}

.layout {
    display: flex;
}

.sidebar {
    padding:30px;
    min-height: 100%;
    width: 400px;
    border-radius: 8px;
    background-color: #1a1a1a;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    margin-right: 20px;
    color: #fff;
    transition: width 0.3s, background-color 0.3s;
}

.sidebar:hover {
    width: 420px;
    background-color: #262626;
}

.content {
    width: 100%;
    height: 100%;
}

.header {
    height: 8vh;
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    transition: background-color 0.3s;
}

.body {
    border-radius: 10px;
    height: 85vh;
    margin-bottom: 0px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    transition: box-shadow 0.3s;
}

.logo h6 {
    font-size: 2rem;
    text-align: center;
    margin: 20px 0;
    color: #fff;
}

.menu {
    margin-top: 20px;
}

.menu-item {
    margin-top: 20px;
    transition: background-color 0.3s;
}

.menu-item a {
    color: #fff;
    text-decoration: none;
    font-size: 1.4rem;
    transition: color 0.3s;
}

.menu-item i {
    font-size: 1.4rem;
    margin-right: 15px;
    transition: color 0.3s;
}

.menu-item:hover {
    background-color: #333;
}

.menu-item.active {
    background-color: #fff;
    padding: 10px;
}

.menu-item.active a,
.menu-item.active i {
    color: #333;
}

.header-content {
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 20px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.4rem;
    transition: color 0.3s;
}

.header-content a {
    text-decoration: none;
    font-size: 1.4rem;
    color: #007bff;
    margin: 0 15px;
    text-transform: uppercase;
    transition: color 0.3s;
}

.header-content a:hover,
.header-content i:hover {
    color: #0056b3;
}
