/* Add these styles to make the form container visually appealing */

body {
    width: 100%;
    height: 100%;
    background: url(https://plus.unsplash.com/premium_photo-1663013439760-cb73ca606ae1?q=80&w=2071&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D) no-repeat center center/cover;
    background-color: rgba(255, 255, 255, 0.8);
    /* Set the last value to adjust transparency (0 for fully transparent, 1 for fully opaque) */
}

.form-container {
    width: 400px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: rgba(245, 245, 245, 0.514);
    /* Adjust the last value for the form container transparency */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    opacity: 0;
    /* Set initial opacity to 0 */
    animation: fadeIn 1s ease-in-out forwards;
    /* Add fade-in animation */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Rest of your styles remain the same */

/* Style the form itself */
.register-form {
    width: 100%;
}

/* Center the form heading */
.register-form h3.text-center {
    text-align: center;
    color: #333;
}

/* Style form items (labels and inputs) */
.register-form .ant-form-item {
    margin-bottom: 16px;
}

/* Style the link to the login page */
.register-form .m-2 {
    display: block;
    text-align: center;
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
}

/* Style the submit button */
.register-form .btn-primary {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

/* Add a hover effect to the submit button */
.register-form .btn-primary:hover {
    background-color: #0056b3;
}